import React, {useState} from 'react'
import * as classes from './Examples.module.scss'
import ex1 from '../../../images/ex1.jpg'
import ex2 from '../../../images/ex2.jpg'
import ex3 from '../../../images/ex3.jpg'
import ex4 from '../../../images/ex4.jpg'
import ex5 from '../../../images/ex5.jpg'
import ex6 from '../../../images/ex6.jpg'
import ex7 from '../../../images/ex7.jpg'
import { MdOutlineKeyboardArrowLeft, MdOutlineKeyboardArrowRight } from 'react-icons/md'
import Slider from '../slider/slider'

export default function Examples() {
    const slides = [
        {   
            name: 'ex1',
            img: ex1
        },
        {
            name: 'ex2',
            img: ex2
        },
        {
            name: 'ex3',
            img: ex3
        },
        {
            name: 'ex4',
            img: ex4
        },
        {
            name: 'ex5',
            img: ex5
        },
        {
            name: 'ex6',
            img: ex6
        },
        {
            name: 'ex7',                                                                        
            img: ex7
        },
    ]


    const [count, setCount] = useState(0);
    const slideItem = (item) => {
        return (
            <div>
                <img src={item.img} alt={item.name} />
            </div>
        )
    }

  return (
    <section id="examples" className={classes.examples}>
        <div className={classes.examples__container}>
            <Slider slides={slides} />
        </div>
    </section>
  )
}
